.containerPresentation{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    z-index: 1;
    position: relative;
    overflow: hidden;
    margin: 0;
}


.profileImg{
    position: relative;
    border-radius: 50%;
    height: 350px;
    margin-bottom: 10px;
    margin-top: 10px;
    z-index: 1;
}


.welcomeMessage{
    font-size: 1.875rem;
    color:rgb(43, 58, 104);
}

.informationStyleOne{
    width: 60%;
    text-align: justify;
}

.myNameStyle{
    font-size: 1.875rem;
    color: white;
    margin-bottom: 15px;
}

.pineBackground{
    max-width: 30%;
    z-index: 0;
}

.ImgBack{
    width: 35%;
    position: relative;
    object-fit: cover;
    height: 100%;
}

@media screen and (max-width: 1300px){
    .profileImg{
        height: 150px!important;
        border: 5px solid #2f2d47!important;
    }
      .containerProfile{
        width: 100%;
    }
}


@media screen and (max-width: 1100px){
    .informationStyleOne{
        width: 80%;
    }
    .containerProfile{
        width: 100%;
    }
    .profileImg{
        height: 100px!important;
    }
    .myNameStyle{
        margin-bottom: 0px;
    }
}



@media screen and (max-width: 800px){
    .informationStyleOne{
        width: 98%;
    }
    .profileImg{
        height: 200px;
    }
    .ImgBack{
        display: none;
    }
    .BackgroundParticles{
        display: none!important;
    }
    .myNameStyle,.welcomeMessage{
        font-size: 1.25rem;
    }

}