.containerFooter{
    display: flex;
    width: 500px;
    height: 500px;
    background: linear-gradient(45deg, blue, red, yellow, green);
    animation: animacion 10s linear infinite;
    background-size: 500%;
}

@keyframes animacion { 
    0%{
        background-position:0 50;
    }
    50%{
        background-position:100% 0;
    }
    100%{
        background-position:0 50;
    }
}