.containerAbilities{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    width: 500px;
    height: 500px;
    background: black;
    border-radius: 20px;
    
}

.containerAbilities::before{
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    background: linear-gradient(45deg,#fb0094,#0000ff, #00ff00, #ffff00, #ff0000);
    width: 100%;
    height: 100%;
    background-size: 500%;
    z-index: -1;
    filter: blur(50px);
    -webkit-animation: spin 5s ease infinite;
    -moz-animation: spin 5s ease infinite;
    animation: asd 5s ease infinite;

}

@-webkit-keyframes asd {
    from {
      background-position: 0px 0px; }
  
    to {
      background-position: 100% 0px; } 
  }

@-webkit-keyframes asd {
    0%{background-position:10% 0%}
    50%{background-position:91% 100%}
    100%{background-position:10% 0%}
}
@-moz-keyframes asd {
    0%{background-position:10% 0%}
    50%{background-position:91% 100%}
    100%{background-position:10% 0%}
}
@keyframes asd { 
    0%{background-position:10% 0%}
    50%{background-position:91% 100%}
    100%{background-position:10% 0%}
}

.backgroundTWo{
    content: "";
    position: absolute;
    background: rgb(15,12,41);
    background: linear-gradient(90deg, rgba(15,12,41,1) 0%, rgba(35,38,64,1) 50%, rgba(15,12,41,1) 100%);
    z-index: -3;
    width: 100%;
    height: 100%;
}



